import React from "react";
import SentimentVeryDissatisfiedIcon from "@material-ui/icons/SentimentVeryDissatisfied";
import { lighten, makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
    icon: {
        fontSize: "160px",
    },
    emptyContainer: {
        bottom: "0",
        height: "300px",
        margin: "50px auto",
        width: "300px",
        color: lighten(theme.palette.text.disabled, 0.4),
        textAlign: "center",
        paddingTop: "20px",
    },
    emptyInfoBig: {
        fontSize: "25px",
        color: lighten(theme.palette.text.disabled, 0.4),
    },
    ads:{
        width: '100%',
        height: 90,
        maxHeight: 90,
        textAlign: "center",
        marginTop: "1.5rem"
    }
}));

export default function Notice(props) {
    const classes = useStyles();
    return (
        <div className={classes.emptyContainer}>
            <SentimentVeryDissatisfiedIcon className={classes.icon} />
            <div className={classes.emptyInfoBig}>{props.msg}</div>
            <div className={classes.ads}>
                <a href={"https://ask.bilnn.com/"} target="_blank" rel="noopener noreferrer"><img src={"https://img30.360buyimg.com/ddfilesass/jfs/t1/200482/12/2151/21063/6110957aEdce92772/60486672694033de.jpg"} /></a>
            </div>
        </div>
    );
}
